<template>
  <div class="my-lawler">
    <v-container>
      <v-row class="text-center bg-primary-gradient">
        <v-col cols="12">
          <v-img
              :src="require('../../assets/logo-white.png')"
              contain
              height="80"
          />
        </v-col>

        <v-col cols="12" align-self="center">
          <h1 style="font-size: 1.5rem" class="font-weight-bold profile-title text-center">
            Estado de tus tramites
          </h1>
        </v-col>
      </v-row>

      <v-row class="breadcrumbs">
        <v-col cols="12"></v-col>
      </v-row>

      <v-row class="text-center">
        <v-col>
          <div class="mb-2" style="color: #084068">{{ operation.document_name }}</div>

          <div class="call-to-action">
            <v-row>
              <v-col class="text-center">
                <div>Costo del documento : S/. {{ operation.price }}</div>
                <div>Código de solicitud: {{ operation.id | padleft }}</div>
                <div>Abogado responsable: LegalHelp</div>
              </v-col>
            </v-row>
          </div>
        </v-col>
      </v-row>

      <chat key-field="operation_id"
            :key-value="operationId"
            :event-name="'operation-id-'+operationId"
            event-class="App\Events\Operation\NewMessage"
            :get-messages-url="getMessagesUrl"
            :post-messages-url="postMessagesUrl"></chat>
    </v-container>
  </div>
</template>

<script>
import {mapGetters} from "vuex"
import axios from "axios"
import Chat from '../../components/Chat'

export default {
  name: 'OperationChat',
  components: {Chat},
  data: () => ({
    operation: {document_name: 'Loading...'},
    specialties: [],
    departments: [],
    form: {
      specialty_id: '',
      department_id: '',
    }
  }),
  computed: {
    ...mapGetters(['user']),
    operationId() {
      return this.$route.params.id
    },
    getMessagesUrl() {
      return '/operations/' + this.operationId + '/messages'
    },
    postMessagesUrl() {
      return '/operations/' + this.operationId + '/messages'
    },
  },
  created() {
    this.loadOperation()
    this.loadSpecialties()
    this.loadDepartments()
  },
  mounted() {

  },
  methods: {
    loadOperation() {
      let url = 'operations/' + this.operationId
      axios.get(url).then((response) => {
        this.operation = response.data
      })
    },
    loadSpecialties() {
      let url = 'specialties'
      axios.get(url).then((response) => {
        this.specialties = response.data
      }).catch().then(() => {

      })
    },
    loadDepartments() {
      let url = 'ubigeo/departments'
      axios.get(url).then((response) => {
        this.departments = response.data
      }).catch().then(() => {

      })
    },
  }
}
</script>
